@import 'color';

.recent_post:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

// For mobile and tablit, width under 1024px 
@media screen and (max-width: 1024px) {
  #container {
    flex-direction: column;
  }
  // Change order of profile from top to bottom
  #container div:first-child {
    order: 1;
  }

  #content {
    width: 100%;
  }

  .aside {
    width: 100%;
  }
}

// For desktop, width over 1024px
@media screen and (min-width: 1025px) {
  #container {
    flex-direction: row;
  }

  #content {
    width: 50%;
  }

  .aside {
    width: 22%;
  }
}

// Common layout properties
/* Contatiner part */
#container {
  display: flex;
  justify-content: space-between;
  margin: 0 20px;
  margin-bottom: 20px;
}

#img-profile {
  border-radius: 50%;
  height: 150px;
  object-fit: cover;
  width: 150px;
}

.btn-email {
  width: 100%;
  border: 0;
  background-color: $color-pink;
  color: white;
  border-radius: 5px;
  height: 1.5em;
  padding: 5px;
  outline: none;
  margin-bottom: 10px;
  font-size: 25px;
}

.btn-email:hover {
  background-color: $color-pink-bold;
}

.wrap-sns {
  margin-top: 10px;
}

.box_sns {
  border-radius: 10%;
  color: black;
  display: inline-block;
  font-size: 30px;
  height: 30px;
  margin-bottom: 20px;
  padding: 10px 0;
  text-align: center;
  width: 22%;
}

.box_sns:hover {
  background-color: $color-pink;
  color: white;
}

#content {
  margin-left: auto;
  margin-right: auto;
}

.recent_post {
  margin: 0 0 10px 0;
  padding: 0;
}

.recent_post:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.box-img-title {
  border-radius: 0.5%;
  margin-bottom: 20px;
}

.img-title {
  border-radius: 10px 10px 0 0;
  max-height: 200px;
  object-fit: cover;
  object-position: 0 30%;
  width: 100%;
}

.box-title_date {
  padding: 0 20px 20px 20px;
}

.aside {
  float: left;
  text-align: center;
  margin-bottom: 10px;
}

/* Text style */
#profile h1 {
  margin-bottom: 0;
}

#profile p {
  margin-top: 10px;
}

h2 {
  color: $color-pink;
  font-weight: bold;
}

time {
  color: $color-gray;
}